body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
}

.web-logo {
    width: 200px;
    transition: 0.3s;
}

.web-logo:hover {
    background-color: #32230F;
}

.eba-font:hover {
    color: #555555;
}

.fa-caret {
    display: inline;
    margin-left: -3px;
}

.dd-toggle {
    color: black;
}

main.content {
    flex: 1 0 auto;
    padding: 10px;
    min-height: 100vh;
}

.footer {
    min-height: 100%;
}

* {
  margin: 0;
  padding: 0;
}

.team-paragraph, .director-paragraph, .funding-paragraph, .student-paragraph {
  font-size: 20px;
  font-weight: 400;
}

main {
  padding: 10px 0px;
}
.get-involved-button {
    text-decoration: none;
    color: white;
}
.markup-tool-instructions {
    border: 2px solid rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
    margin-top: -20px;
    background-color: #C8C8C8;
}

.instruction-text {
    padding-top: 20px;
    color: black;
}

.markup-tool-title {
    font-size: 35px;
    font-weight: 600;
    padding-bottom: 30px
}

button.clear-form-button {
    background-color: #D3D3D3;
}

button.submit-form-button {
    background-color: #DED7B9;
}

button.clear-form-button, button.submit-form-button, button.back-button {
    padding: 10px;
    margin: 5px;
    border-radius: 10px;
    width: 15%;
    border: none;
    transition: 0.3s;
}

button.clear-form-button:hover, button.submit-form-button:hover, button.back-button {
    background-color: #555555;
    color: white;
}

.required-field {
    color: #5A5E61;
}

.success {
    text-decoration: none;
    font-weight: 300;
    color: #2E8B57;
}

.xml-output {
    padding-top: 10px;
    margin: 10px;
}

.form-label {
    padding: 10px;
}

.input, .input-txt {
    width: 100%;
}

.input-txt {
    height: 200px;
    margin-bottom: 20px;
}

.input-btn {
    background-color: #DED7B9;
    color: black;
    border: none;
    border-radius: 5px;
    height: 150%;
    transition: 0.3s;
    margin-top: -20px;
}

.input-btn:hover {
    background-color: #555555;
    color: white;
}

.sent-successful, .sent-error {
    display: none;
    color: white;
    justify-content: center;
    text-align: center;
    border-radius: 5px;
}

.sent-successful {
    background-color: #50C878;
}

.sent-error {
    background-color: #CF142B;
}
* {
    margin: 0;
    padding: 0;
}

html {
    font-size: 100%;
}

.row {
    height: 100%;
    padding-bottom: 35px;
}

main {
    padding: 10px 0px;
}

.about-paragraph {
    font-size: 20px;
    font-weight: 400;
}

.about-row {
    flex-direction: row-reverse;
}

.bold {
    font-style: normal;
    font-weight: 800;
}

.our-goals {
    padding-top: 100px;
}

.text-center {
    text-align: center;
    border-bottom: 1px solid gray;
    width: 100%;
    display: block;
}

.dark-title-button {
    background: black;
    height: 120px;
    text-align: center;
    color: white;
    padding-top: 20px;
    transition: 100ms linear all;
}

.dark-title-button > p {
    display: none;
}

.dark-title-button-open {
    height: 300px;
    text-align: center;
    color: white;
    padding-top: 20px;
    transition: 100ms linear all;
}

.dark-title-button-open > p {
    display: block;
    margin: 0px 15px;
}

.dark-title-button:hover {
    cursor: pointer;
}

.arrow-white {
    height: 35px;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    transition: 175ms linear all;
}

.rotated {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: 175ms linear all;
    margin-bottom: -70px;
}

.view-team-button {
    color: black;
}

.view-team-button:hover {
    font-weight: 750;
}
.text {
    color: #DED7B9;
    padding: 22px 20px;
}

.container-fluid.footer {
    padding: 10px;
    background-color: black;
}

.follow-title {
    justify-content: center;
}

.icons {
    padding: 8px 10px;
}

div > a.footer-link {
    text-decoration: none;
    color: #DED7B9;
}
